export function hasOwnProperty(obj, key) {
    return {}.hasOwnProperty.call(obj, key);
}
export function getValueAtIndexOrReturn(value, index, defaultValue) {
    if (Array.isArray(value)) {
        const v = value[index];
        return v == null
            ? Array.isArray(defaultValue)
                ? defaultValue[index]
                : defaultValue
            : v;
    }
    return value;
}
export function isType(value, type) {
    const str = {}.toString.call(value);
    return str.indexOf('[object') === 0 && str.indexOf(`${type}]`) > -1;
}
export function invokeWithArgsOrReturn(value, args) {
    return typeof value === 'function' ? value(...args) : value;
}
export function debounce(fn, ms) {
    // Avoid wrapping in `setTimeout` if ms is 0 anyway
    if (ms === 0) {
        return fn;
    }
    let timeout;
    return (arg) => {
        clearTimeout(timeout);
        timeout = setTimeout(() => {
            fn(arg);
        }, ms);
    };
}
export function removeProperties(obj, keys) {
    const clone = Object.assign({}, obj);
    keys.forEach((key) => {
        delete clone[key];
    });
    return clone;
}
export function splitBySpaces(value) {
    return value.split(/\s+/).filter(Boolean);
}
export function normalizeToArray(value) {
    return [].concat(value);
}
export function pushIfUnique(arr, value) {
    if (arr.indexOf(value) === -1) {
        arr.push(value);
    }
}
export function appendPxIfNumber(value) {
    return typeof value === 'number' ? `${value}px` : value;
}
export function unique(arr) {
    return arr.filter((item, index) => arr.indexOf(item) === index);
}
export function getNumber(value) {
    return typeof value === 'number' ? value : parseFloat(value);
}
export function getBasePlacement(placement) {
    return placement.split('-')[0];
}
export function arrayFrom(value) {
    return [].slice.call(value);
}
export function removeUndefinedProps(obj) {
    return Object.keys(obj).reduce((acc, key) => {
        if (obj[key] !== undefined) {
            acc[key] = obj[key];
        }
        return acc;
    }, {});
}
